import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'
export default {
	data() {
		return {
			carouselImg:[],
			goodsInfo: {},
			content: '',
			
			options: [
				{
					icon: 'headphones',
					text: '客服'
				}, {
						icon: 'shop',
						text: '店铺',
						info: 0,
						infoBackgroundColor:'#007aff',
						infoColor:"red"
				}, {
						icon: 'cart',
						text: '购物车',
						info: 2
				}
			],
				
			buttonGroup: [
				{
					text: '加入购物车',
					backgroundColor: '#ff0000',
					color: '#fff'
				},
				{
					text: '立即购买',
					backgroundColor: '#ffa200',
					color: '#fff'
				}
			]
		}
	},
	onLoad(options) {
		this.getDetailImg(options.id)
		this.getGoodsInfo(options.id)
		this.getGoodsContent(options.id)
	},
	methods: {
		getDetailImg(id) {
			this.$api.getDetailImg(id)
			.then((res) => {
				this.carouselImg = res.message
			})
		},
		getGoodsInfo(id) {
			this.$api.getGoodsInfo(id)
			.then((res) => {
				this.goodsInfo = res.message[0]
			})
		},
		getGoodsContent(id) {
			this.$api.getGoodsContent(id)
			.then((res) => {
				this.content = res.message[0].content
			})
		},
		
		onClick (e) {
			uni.showToast({
				title: `点击${e.content.text}`,
				icon: 'none'
			})
		},
		buttonClick (e) {
			console.log(e)
			this.options[2].info++
		}
	},
	components:{
		uniGoodsNav
	}
}
